import manJson from "./man.json"
import womanJson from "./woman.json"

/// 기본 옷
import defaultModelJson from "./defaultModel.json"

// 교체하기
//import modelJson from "./model.json"

import * as THREE from 'three'
import * as React from 'react'
import { Canvas, useLoader } from '@react-three/fiber'
import { OrbitControls } from '@react-three/drei'
import { useFBX } from '@react-three/drei'
import { TextureLoader } from 'three/src/loaders/TextureLoader'

type View3DProps = {
  data: any;
};

function ChnageModel(data:any):void
{
  let modelJson = data.data;
  console.log("modelJson:"+modelJson);

   for(let i:number = 0;i< defaultModelJson.part.length;i++)
   {
      for(let j:number =0;j<modelJson.part.length;j++)
      {
        if ( modelJson.part[j].partType == defaultModelJson.part[i].partType )
        {
            defaultModelJson.part[i].avatarParts = modelJson.part[j].avatarParts;
            defaultModelJson.part[i].img =  modelJson.part[j].img;
        }
      }
   }

   let IsOnepiece:boolean = false;
   for(let j:number =0;j<modelJson.part.length;j++)
   {
     if ( modelJson.part[j].partType == 6 && modelJson.part[j].avatarParts!=0)
     {
        IsOnepiece = true;
        break;
     }
   }
   
   if (IsOnepiece==true)
   {
    for(let i:number = 0;i< defaultModelJson.part.length;i++)
    {
          if (  defaultModelJson.part[i].partType ==3 || defaultModelJson.part[i].partType ==4 )
          {
              defaultModelJson.part[i].avatarParts = 0
          }
    }
   }
}


function GetTextureString(model:any):any
{
   //const urlBase:string ='https://cdn-avatar-studio.epiclive-platform.com/dev/avatarPart/'
   const urlBase:string =''
   const dictMap : any = {};

   for(let i:number = 0;i< defaultModelJson.part.length;i++)
   {
      for(let j:number =0;j<model.partInfo.length;j++)
      {
        if ( model.partInfo[j].partType == defaultModelJson.part[i].partType && defaultModelJson.part[i].avatarParts!=0 )
        {
            for(let k:number =0;k<model.partInfo[j].avatarParts[ defaultModelJson.part[i].avatarParts-1].material.length;k++)
            {
              let mat = model.partInfo[j].avatarParts[ defaultModelJson.part[i].avatarParts-1].material[k];

               if (mat==model.partInfo[j].avatarParts[ defaultModelJson.part[i].avatarParts-1].baseMesh[0] )
               {
                  if ( defaultModelJson.part[i].img.length > 0 )
                  {
                    dictMap[mat]=defaultModelJson.part[i].img.replace('.png','')+'.png';
                  }else{
                    dictMap[mat]=urlBase+mat.replace('.png','')+'.png';
                  }
               }else{
                  dictMap[mat]=urlBase+mat.replace('.png','')+'.png';
               }
            }
        }
      }
   }
   //man_basebody_foot
   return dictMap;
}

function GetVisibleList(model:any):any
{
   const dictMap : any = {};

   for(let i:number = 0;i< defaultModelJson.part.length;i++)
   {
      for(let j:number =0;j<model.partInfo.length;j++)
      {
        if ( model.partInfo[j].partType == defaultModelJson.part[i].partType && defaultModelJson.part[i].avatarParts!=0 )
        {
          for(let k:number =0;k<model.partInfo[j].avatarParts[ defaultModelJson.part[i].avatarParts-1].baseMesh.length;k++)
          {
            dictMap[model.partInfo[j].avatarParts[ defaultModelJson.part[i].avatarParts-1].baseMesh[k]] = model.partInfo[j].avatarParts[ defaultModelJson.part[i].avatarParts-1].material[k];
          }
        }
      }
   }
   return dictMap;
}


//function Human(props: JSX.IntrinsicElements['mesh']) {
  function Human(data: any) {

  let modelData:any;
  let fbxFile:string="";
  if ( defaultModelJson.modelType =="man" )
  {
    modelData = manJson;
    //fbxFile ='https://cdn-avatar-studio.epiclive-platform.com/dev/avatarPart/s_m_costume_sample.fbx';
              
    fbxFile ='s_m_costume_sample(no_png).fbx';
  }else if ( defaultModelJson.modelType =="woman" )
  {
    modelData = womanJson;
    //fbxFile ='https://cdn-avatar-studio.epiclive-platform.com/dev/avatarPart/s_m_costume_sample.fbx';
    fbxFile ='s_m_costume_sample(no_png).fbx';
  }

  ChnageModel(data);

  // 텍스처 리스트
  let urlDic = GetTextureString(modelData);

  let urlList:Array<string>=new Array<string>();

  for (var key in urlDic) {
    if (urlDic.hasOwnProperty(key)) {
      urlList.push(urlDic[key] );
      //console.log("urlList:"+key+":"+ urlDic[key]);
    }
  }
  const fbx = useFBX(fbxFile)
  // 텍스처로딩
  let textList:any = useLoader(TextureLoader, urlList)
  
  const dictMap : any = {};
  let indexTex:number = 0;
  for (var key in urlDic) {
    if (urlDic.hasOwnProperty(key)) {
      dictMap[key] = textList[indexTex];
      indexTex++;
    }
  }

  let viewList:any = GetVisibleList(modelData);
  
    fbx.children.forEach((mesh, i) => {
    if ( mesh instanceof THREE.Mesh)
    {

      if ( viewList.hasOwnProperty(mesh.name) ==true )
      {
         let mat = viewList[mesh.name]

         console.log("mesh:"+mesh.name+":"+mat);

          let cloneMat = new THREE.MeshStandardMaterial({
              color: mesh.material.color,
              map:dictMap[mat],
              emissive:mesh.material.emissive,
              emissiveIntensity:mesh.material.emissiveIntensity,
              roughness:mesh.material.roughness,
              metalness:mesh.material.metalness
          });
         
          mesh.material = cloneMat;
      }else{
        mesh.visible=false;
      }
    }
  });


  return (
     <primitive object={fbx} position={[0, -100, 0]} />
  )
}

function View3D({data}:View3DProps) {

  return (
    <div style={{ width: "100vh" , height: "100vh" }}>
    <Canvas camera={{ position: [-15, 0, 500], fov: 25 }} style={{ backgroundColor: "white" }} >
    <OrbitControls autoRotate autoRotateSpeed={0.05} enableZoom={false} makeDefault minPolarAngle={Math.PI / 2} maxPolarAngle={Math.PI / 2} />
    <directionalLight position={[10, 10, 10]} intensity={1.5} />
      <ambientLight intensity={0.2} />
      <Human data={data} />
    </Canvas>
    </div>
  )
}

export default View3D;